.sectionheading {
    color: floralwhite;
    text-align: center;
    font-weight: bolder;
    font-size: larger;
}

.sectiondata{
    color: bisque;
    text-align: center;
}



.imgsection {
    margin: 20px;
}

table {
    margin-left: auto;
    margin-right: auto;
    width: 70%;
}


table img {
    border-radius: 15px;
    transition: transform 0.3s ease-in-out;
    height: 250px;
    width: 270px;
}

table img:hover {
    transform: scale(1.1);
}

td {
    padding: 10px;
}
