.image-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    gap: 10px; 
    padding: 10px;
  }

  .image-grid img {
    max-width: 75%; 
    width: 400px;
    height: 200px; 
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
  }

  .image-grid img:hover {
    transform: scale(1.1); 
  }