.body{
    padding: none;
    color: aqua;
    border-style: double;
    border-radius: 20px;
    margin-top: 50px;
    margin-bottom: 40px;
    border-color: azure;
    margin-left: auto;
    margin-right: auto;
    max-width: 80%;
    height: auto;
    background: linear-gradient(to bottom, #000000, #000033, #000066, #000099, #0000cc);
    
}



.intro{
    margin-left: auto;
    margin-right: auto;
    width: 95%;
}

.list-table{
    font-size: large;
}

.list-table img{
    height: 150px;
    width: 200px;
    border-radius: 10px;
    box-shadow: 0 0 10px aquamarine;
    cursor: pointer;
}

.picture-para img{
    height: 200px;
    width: 250px;
    border-radius: 10px;
}

.picture-para p{
    font-size: large;
}

.picture-para{
    margin-bottom: 50px;
}

.buttons {
    text-align: center;
    margin-top: 20px; 
  }
  
  .window {
    display: inline-block;
    margin: 0 10px; 
    border-radius: 20px;
    background: linear-gradient(to right, #3498db, #2c3e50); 
    color: #fff;
    text-decoration: none;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease; 
  }
  
  .window:hover {
    background: linear-gradient(to right, #2c3e50, #3498db); 
  }
  