.image-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .image-container img {
    margin: 0 10px; 
    transition: transform 0.3s;
    padding: 10px;
    border-radius: 25px;
  }

  .image-container img:hover {
    transform: scale(1.1); 
  }