.rearth {
  position: absolute;
  top: 0;
  left: 0;
  animation: horizontalRotation 4s linear infinite;
  width: 200px;
}

@keyframes horizontalRotation {
  0% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(-1);
  }
}

.header h2{
text-align: center;
align-items: center;
justify-content: center;
}

.navbar {
background-color: black;
padding: 10px;
top: 0;
right: 0;
}

.nav-list {
list-style: none;
margin: 0;
padding: 0;
display: flex;
justify-content: flex-end; 
align-items: center;
}

.right {
margin-left: 15px;
}

.design {
border: none;
outline: none;
background-color: rgb(97, 201, 201);
border-radius: 15px;
text-align: center;
font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
font-weight: lighter;
font-size: larger;
cursor: pointer;
}

.datasection {
margin-top: 190px; 
}

 table{
width: 80%;
border-collapse: collapse;
margin-top: 20px; 
font-size: larger;
margin-left: auto;
margin-right: auto;
}


table, th, td {
border: none;
}

th, td {
padding: 10px;
text-align: center;
}

.sections section{
margin-top: 50px;
font-size: larger;
padding: 0px;
}

.pic{

border: none;
outline: none;
border-radius: 15px;
margin-left: auto;
margin-right: auto;
margin-bottom: 10px;
display: block;
height: 200px;
width: 300px;
box-shadow: 0 0 10px rgba(0, 0, 255, 0.5);
cursor: pointer;
transition: box-shadow 0.3s ease-in-out;
}

.pic:hover{
box-shadow: 0 0 20px rgba(0, 0, 255, 0.7);
}

.graph_image{
  border: none;
  outline: none;
  border-radius: 15px;
  display: block;
  height: 200px;
  width: 250px;
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  transition: 0.5s;
}

.datasection img:hover{
  transform: scale(1.5);
}

.contacts table {
  border-collapse: collapse;
  color: aliceblue;
  margin-right: 320px;
}

.contacts table, .contacts td, .contacts th {
  border: none;
}

.contacts td, .contacts th{
  padding: 5px;
  text-align: center;
}

.footer p{
  margin-bottom: 10px;
}

.footer ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.footer li {
  margin: 0;
}

.footer button {
  background-color: rgb(97, 201, 201);
  border: none;
  outline: none;
  border-radius: 15px;
  text-align: center;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: lighter;
  font-size: large;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
  padding: 5px 10px; 
}

.footer button:hover {
  background-color: rgb(77, 181, 181);
}

@media screen and (max-width: 768px) {
  .footer button {
    font-size: smaller; 
    padding: 3px 5px; 
  }
}

.footer {
  position: relative;
  color: white;
  padding: 0px;
  overflow: hidden;
}

.number{
  font-weight: bolder;
  color: aliceblue;
}

.footer-video {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height: 350px;
  object-fit: cover;
}

.text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 1;
  width: 100%;
}

.name{
  font-weight: 900;
  color: gold;
  font-size: larger;
}

.form-para{
  color: chartreuse;
}

.contacts p{
  margin-right: 200px;
}
