.imagecontainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px; 
    padding: 10px;
  }

  .imagecontainer img {
    max-width: 100%;
    height: auto;
    display: block;
    border-radius: 20px;
    height: 200px;
    width: 350px;
    transition: transform 0.3s ease-in-out;
  }

  .imagecontainer img:hover {
    transform: scale(1.1);
  }