.gallery-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; 
}

.gallery-item {
  width: 20%; 
  margin: 10px;
  text-align: center;
}

.gallery-item img{
  border-radius: 10px;
}

.gallery-image {
  width: 100%;
}

.image-text {
  margin-top: 5px;
  font-size: 14px; 
}

.show-more-btn {
  margin-top: 20px;
  padding: 10px 20px;
  background: linear-gradient(to right, #4b6cb7, #182848);
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.3s ease;
  margin-bottom: 10px;
  margin-left: 20px;
}

.show-more-btn:hover {
  background: linear-gradient(to right, #182848, #4b6cb7);
}